.nav {
  position: fixed;
  top: 0;
  padding: 20px;
  width: 100%;
  height: 30px;
  z-index: 9999;

  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav__black {
  background-color: black;
}

.nav__contents {
  display: flex;
  justify-content: space-between;
}

.nav__logo {
  position: absolute;
  top: 10px;
  left: 0;
  width: 100px;
  object-fit: contain;
  padding-left: 20px;
  cursor: pointer;
}

.nav__avatar {
  cursor: pointer;
  position: absolute;
  right: 0;
  padding-right: 60px;
  width: 40px;
  object-fit: contain;
}
